<template>
  <div >
    <div>
      <vue-scroll-progress-bar
        backgroundColor="#000000"
        
        height="0.3rem"
      />
    </div>

    <v-progress-linear
      :indeterminate="loading"
      :active="loading"
      color="red darken-2"
    ></v-progress-linear>

    <!-- desktop -->
    <v-container class="mt-10" style="    margin-bottom: 3%;">
      <v-row
        v-if="this.mode != 'delete'"
        class="mt-4 animated bounceInLeft slow"
        style="text-align: -webkit-center"
      >
        <v-col cols="4" sm-4 v-for="items in array_getdata" :key="items">
          <v-hover v-slot:default="{ hover }" open-delay="200">
            <v-card
              class="mt-4 mx-auto"
              max-width="340"
              :elevation="hover ? 16 : 2"
              @click="Open_device(items)"
            >
              <v-sheet
                color="light-blue darken-2"
                class="v-sheet--offset mx-auto white--text"
                elevation="12"
                max-width="calc(100% - 32px)"
              >
                <v-row>
                  <v-col></v-col>
                </v-row>
                <v-row align="center" justify="left">
                  <v-col cols="9">
                    <div
                      class="font-weight-bold truncate"
                      style="font-size: 21px"
                    >
                      {{ items.machineName }}
                    </div>
                  </v-col>

                  <v-col cols="2">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-icon large v-on="on" color="white"
                          >mdi-flash-circle</v-icon
                        >
                      </template>
                      <span>img device</span>
                    </v-tooltip>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col></v-col>
                </v-row>
              </v-sheet>
              <v-card-text class="pt-0 left">
                <div
                  class="mb-2 ml-2 font-weight-medium truncate"
                  style="text-align-last: left; font-size: 15px"
                >
                  <span style="font-size: 16px">Department:</span>
                  {{ items.department }}
                </div>

                <div
                  class="mt-3 mb-1 h5 font-weight-medium ml-2 truncate"
                  style="text-align-last: left; font-size: 15px"
                >
                  <span style="font-size: 16px">MachineID:</span>
                  {{ items.machineID }}
                </div>
              </v-card-text>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
      <!-- delete -->
      <v-row
        v-if="this.mode == 'delete'"
        style="text-align: -webkit-center"
        class="mt-2"
      >
        <v-col cols="4" sm-4 v-for="items in array_getdata" :key="items">
          <v-hover v-slot:default="{ hover }" open-delay="200">
            <v-card
              class="mt-5 mx-auto"
              max-width="320"
              @click="Open_delete_Device(items)"
              :elevation="hover ? 16 : 2"
            >
              <v-sheet
                color="black"
                class="v-sheet--offset mx-auto white--text"
                elevation="12"
                max-width="calc(100% - 32px)"
              >
                <v-row>
                  <v-col></v-col>
                </v-row>
                <v-row align="center" justify="left">
                  <v-col cols="9">
                    <div
                      class="font-weight-bold truncate"
                      style="font-size: 21px"
                    >
                      {{ items.machineName }}
                    </div>
                  </v-col>

                  <v-col cols="2">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-icon large v-on="on" color="white"
                          >mdi-flash-circle</v-icon
                        >
                      </template>
                      <span>img device</span>
                    </v-tooltip>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col></v-col>
                </v-row>
              </v-sheet>
              <v-card-text class="pt-0 left">
                <div
                  class="title font-weight-light mb-2 ml-2"
                  style="text-align-last: left"
                >
                  {{ items.department }}
                </div>
                <div
                  class="subheading font-weight-light grey--text ml-2"
                  style="text-align-last: left"
                >
                  {{ items.machineID }}
                </div>
                <v-divider class="my-2"></v-divider>
              </v-card-text>
              <v-fade-transition class="animate slideInUp">
                <v-overlay
                  v-if="hover"
                  absolute
                  color="grey lighten-1"
                  opacity="0"
                >
                  <v-icon size="80px" style="margin-top: 20%" color="red"
                    >mdi-close-circle-outline</v-icon
                  >
                </v-overlay>
              </v-fade-transition>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
      <v-row align="center" justify="center" class="ml-2 mr-5">
        <v-col
          class="mt-0"
          cols="6"
          style="text-align: -webkit-center"
          height="250px"
        ></v-col>
        <!-- <v-col cols="6" sm="6" md="6" height="400px">
          <v-card flat style="background-color: #eceff1">
            <v-card-text></v-card-text>
          </v-card>
        </v-col> -->
      </v-row>
    </v-container>
    <v-speed-dial
      class="mb-12 mr-1"
      v-model="create"
      fab
      fixed
      right
      bottom
      hover
      :transition="transition"
    >
      <template v-slot:activator>
        <v-btn
          v-if="mode === 'create'"
          v-model="create"
          bottom
          large
          color="blue darken-2"
          dark
          fab
        >
          <v-icon v-if="create" large>mdi-close</v-icon>
          <v-icon v-else large>mdi-plus-circle</v-icon>
        </v-btn>

        <v-tooltip v-model="show" left v-if="mode != 'create'">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="exit_mode_delete()"
              bottom
              large
              color="black darken-2"
              dark
              fab
            >
              <v-icon>mdi-arrow-right-bold</v-icon>
            </v-btn>
          </template>
          <span>Exit Delete Mode</span>
        </v-tooltip>
      </template>
      <v-btn fab dark color="green" @click="btn_create()">
        <v-icon>mdi-plus</v-icon>
      </v-btn>
       <v-btn fab dark color="red" @click="btn_delete()">
        <v-icon>mdi-delete</v-icon>
      </v-btn>
    </v-speed-dial>
    <DialogCreateDevice
      ref="DialogCreateDevice"
      @getListdevice="getdata_Listdevice"
    ></DialogCreateDevice>
  </div>
</template>

<script>
import DialogCreateDevice from "@/components/DialogCreateDevice.vue";
import Swal from "sweetalert2";
export default {
  name: "DashboardDevice",
  components: {
    DialogCreateDevice,
  },
  data() {
    return {
      counters:0,
      alert: false,
      Index: "",
      attrs: undefined,
      Name: "",
      fab: false,
      loading: false,
      array_getdata: [],
      mode: "create",
      create: "",
      loadingdelete: false,
      color_delete: "",
      show: false,
      show1: false,
      show2: false,
      transition: "slide-y-reverse-transition",
      array_devicedata: [],
    };
  },
  created() {
    this.getdata_Listdevice();
    this.$store.commit("data_interval_get_data", this.counters);
  },
  methods: {
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    Open_device(items) {
      var arrayDevice = this.array_devicedata;
      // console.log(this.typeDevice);
      var Device = {
        Index: arrayDevice.length + 1,
        machineID: items.machineID,
        machineName: items.machineName,
        department: items.department,
      };
      arrayDevice.push(Device);
      window.localStorage.setItem("arrayDevice", JSON.stringify(arrayDevice));

      this.$router.push("/Device");
    },
    Open_delete_Device(items) {
      // console.log(items.machineID);
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.value) {
          this.API_Delete_Device(items.machineID)
        }
      });
    },
    API_Delete_Device(machineID){
      // console.log(machineID);
       this.axios({
        method: "DELETE",

        // /api/v1/meters?meter_id=<meter_id
        url: this.$store.state.url_axios + "/api/v1/meters?meter_id=" + machineID,
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(response => {
        console.log(response);
        this.resultcheck = response.data;
        this.result = response.data.result;
       
        if (this.resultcheck["message"] === "success") {
          this.$emit('getListdevice')
          Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'Delete device success',
            })
          this.getdata_Listdevice()  
        }
        else {
          Swal.fire({
            icon: "error",
            title: "error",
            text: "Can not be deleted",
          });
        }
      }).catch(error => {
        // console.log(error.response);
          if (error.response.data.error_message === "Measurement Not Found") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "ไม่พบข้อมูลกราฟ วัน-เวลาที่ค้นหา !!"
            });
          }
               else   if (error.response.data.error_message === "Meter exist") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Duplicate meter name Can not be added",
            });
          }
           else {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "กรุณาตรวจสอบการเชื่อมต่อ"
            });
          }
        });
    },
    btn_create() {
      this.$refs.DialogCreateDevice.open();
    },
    btn_delete() {
      this.mode = "delete";
      this.snackbar_delete = true;
      this.$message.error("Delete Mode");
    },
    exit_mode_delete() {
      this.mode = "create";
      this.create = "";
    },
    getdata_Listdevice() {
      // console.log("getdata_Listdevice");
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/meters",
        headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log(response);
          this.loading = false;
          var result = response.data;

          if (result.len === 0) {
            this.$swal({ title: "ไม่มีข้อมูล", type: "info" });

            this.array_getdata = result["result"];
            this.loading = false;
          } else {
            this.array_getdata = result["result"];
            this.loading = false;

            // console.log(this.array_getdata);
          }
        })
        .catch((error) => {
           console.log(error);
          if (error.response.data.error_message === "Measurement Not Found") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "No data",
            });
          } else if (error.response.statusText === "UNAUTHORIZED") {
            // console.log("Ddddddddddddd");
            this.$router.push("/login");
            this.$message.warning("Time out !!!");
          } else if (error.response.statusText === "UNPROCESSABLE ENTITY") {
            this.$router.push("/login");
          } else {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "กรุณาตรวจสอบการเชื่อมต่อ",
            });
          }
        });
    },
  },
};
</script>

<style scoped>
.size_width {
  max-width: 300px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.v-sheet--offset {
  margin: 2%;
  top: -14px;
  position: relative;
}

.truncate {
  max-width: 400px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
